:root {
  --yellow: #F5C32C;
  --orange: #FCA61F;
  --black: #242D49;
  --gray: #788097;
  --blueCard: #DDF8FE;
  --purple: rgb(238 210 255);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  width: 100vw;
  overflow: hidden;
  background-image: url(/[TatsumiP].github.io/static/media/denim.cf0a9ad0.webp);
}

/* ------- heading css -------*/


.heading-container {
  height: 150px;
  max-width: 1400px;
}

.heading-board {
  box-sizing: border-box;
  display: flex;
  flex: 1 1;
  position: relative;
  width: 20rem;
  height: 50%;
  top: 30%;
  background: linear-gradient(150deg, #e0dbb2 50.71%, #866b46 99.76%);
  border: 4px solid #B4C1C6;
  box-shadow: 1px 4px 3px rgba(0, 0, 0, 0.8);
  border-radius: 0px 62px 62px 0px;
}

.heading-board img {
  display: inline;
  position: relative;
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
}


.heading-board h2 {
  display: inline;
  position: relative;
  margin: auto 2rem;
  top: 2px;
  font-weight: bold;
  font-size: 30px;
  color: #443f08;
}

/* -----grid css ----- */

.grid {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.grig-item {
  list-style: none;
}

.grid-col-2>.grid-item {
  width: 50%;
}

.grid-col-3>.grid-item {
  width: 33.3%;
}

.grid-col-4>.grid-item {
  width: 25%;
}

.feature {
  text-align: center;
  padding: 0 15px;
}

.feature-headline {
  font-weight: bold;
  margin: 0 0 20px;
}

.feature-img {
  margin: 0 0 20px;
}

.feature-description {
  margin: 0;
  text-align: left;
}

/* ----- animetion css -----*/

.fadein {
  position: relative;
  opacity: 0;
  -webkit-animation-name: fadein;
          animation-name: fadein;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }
}

/* ----- module css ----- */

.module-spacer-s {
  height: 1.5rem;
}

.module-spacer-m {
  height: 3rem;
}

.module-spacer-l {
  height: 5rem;
}

.module-liner-s {
  border: solid #000000 1px;
  width: 60%;
}

/* ------- component css ------- */

.button {
  background: linear-gradient(180deg, #ffbb01 26.71%, #d47816 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
  border-radius: 34px;
  border: 1px solid #8a4602;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
}

.button:hover {
  background: white;
  cursor: pointer;
  border: 1px solid #FCA61F;
  border: 1px solid var(--orange);
  color: #FCA61F;
  color: var(--orange);
  box-shadow: 0 0 0 #bbb;
}

@media screen and (max-width: 560px) {

  /*  ----- for IPhone ----- */
  .App {
    padding: 0.5rem 1rem;
  }
}

@media screen and (min-width: 561px) and (max-width: 768px) {

  /*  ----- for Tablet ----- */
  .App {
    padding: 0;
  }
}
