/* ----- toggle-menu ----- */
.toggle-menu {
  position: absolute;
  height: 
  100vh;
  width: 100vw;
  max-width: 1400px;
  margin: 0 auto;;
  padding: 0;
  top: -100%;
  z-index: 105;
}

.toggle-menu:hover {
  cursor: pointer;
}

/* toggle-button */
#toggle-button {
  display: block;
  height: 25px;
  width: 40px;
  margin-left: 10px;
  padding-top: 20px;
  z-index: 110;
}

#toggle-button span,
#toggle-button span:before,
#toggle-button span:after {
  content: "";
  display: block;
  height: 4px;
  width: 38px;
  border-radius: 3px;
  background-color: #ffffff;
  position: absolute;
  z-index: 110;
}
#toggle-button span:before {
  bottom: 11px;
}
#toggle-button span:after {
  top: 11px;
}
/* invisible-checkbox function */
#toggle-check {
  display: none;
}


