}
#navbar ul li:first-child {
  border-radius: 0px;
}
#navbar ul li:nth-child(5) {
  width: 8.1rem;
  height: 3rem;
  left: 0px;
  top:  -1px;
  padding: 0;
  border-radius: 0;
}
#navbar ul li:nth-child(5) img {
  width: 25px;
  height: 25px;
  margin: -5px auto 5px;
}
#navbar ul li:nth-child(5) h3 {
  font-size: 0.8rem;
  margin: 0 5px 0 0;
}

  /* ----- toggle-menu ----- */
.toggle-menu {
  display: block;
  position: fixed;
  top: 8%;
  left: 4%;
}
#toggle-check:checked~#navbar {
  left: -5%;
  background-color:rgba(0, 0, 0, 0.4);
}
}


